import os from "os"
import { secondMs } from "../../fe/Date"
import { defer } from "../../fe/Defer"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { clamp, gte, gte0, mapFinite } from "../../fe/Number"
import { lazy } from "../Lazy"
import { mkLogger } from "../Logger"
import { isTest } from "../NodeEnv"
import { EndableRanks } from "../async/Endable"
import { EndableInterval } from "../async/EndableInterval"
import { InternalErrorFlag } from "../error/ErrorFlags"
import { Average } from "../math/Average"
import { isWin } from "../platform/Platform"
import { Settings } from "../settings/Settings"
import { cpuCount } from "./CpuInfo"
import { singleThreadMode } from "./MaxCpus"

const logger = lazy(() => mkLogger("work.CpuUsage"))

const logTooBusy = lazy(
  () => {
    logger().warn("Current system load is too high to schedule more work", {
      currentBusyPct: CpuUsage.instance().busyPct(),
      max: Settings.cpuBusyPercent.valueOrDefault
    })
  },
  (isTest ? 15 : 60) * secondMs
)

export function isTooBusy(): boolean {
  const busyPct = CpuUsage.instance().busyPct()
  // If people set cpuLoadPercent too low, this results in PhotoStructure
  // never running. Only set this if the setting is reasonable.

  // min use would be 50% of one cpu, so, 100 / (2 * cpuCount):
  const reasonableMaxCpuPct = clamp(
    Math.round(100 / (2 * cpuCount())),
    125,
    Math.round(Settings.cpuBusyPercent.valueOrDefault)
  )
  if (gte(busyPct, reasonableMaxCpuPct)) {
    logTooBusy()
    return true
  } else {
    logTooBusy.unset() // < log the next time we're too high
    return false
  }
}

/**
 * Watches and reports system cpu utilization (as opposed to process.cpuUsage,
 * which returns this process' utilization)
 */
export class CpuUsage extends EndableInterval {
  private readonly busyPctAvg = new Average(5)
  private last = new CpuTimes()

  static instance = defer(() => new CpuUsage())

  /**
   * @param intervalMs most OSes don't seem to update _os.CpuInfo more than
   * twice a second, so 500ms is a reasonable default
   * @param sampleCount larger numbers give more smoothing. 1.5 seconds of
   * smoothing should be fine. 1 disables smoothing.
   */
  constructor() {
    super({
      name: "CpuUsage()",
      callback: () => this.#sample(),
      intervalMs: (singleThreadMode() ? 15 : 2.5) * secondMs,
      rank: EndableRanks.first
    })
    mapFinite(busyPctFromLoad(), ea => this.busyPctAvg.push(ea))

    // set .last:
    this.#sample()
  }

  get cpuCount() {
    return this.last.cpuCount
  }

  /**
   * weighted average percent of the time CPUs are idle
   */
  idlePct(): Maybe<number> {
    return map(this.busyPct(), ea => 100 - ea)
  }

  busyPct(): Maybe<number> {
    return this.busyPctAvg.sampleAvg ?? busyPctFromLoad()
  }

  #pushBusyPct(ea: Maybe<number>) {
    if (gte0(ea)) this.busyPctAvg.push(ea)
  }

  #sample(): void {
    this.#pushBusyPct(busyPctFromLoad())
    const now = new CpuTimes()
    this.#pushBusyPct(now.busyPct(this.last))
    this.last = now
  }
}

class CpuTimes {
  readonly cpuCount: number
  readonly busyMs: number
  readonly idleMs: number

  constructor(cpus: os.CpuInfo[] = os.cpus()) {
    let busyMs = 0
    let idleMs = 0
    for (const ea of cpus) {
      busyMs += ea.times.user + ea.times.nice + ea.times.sys + ea.times.irq
      idleMs += ea.times.idle
    }
    this.busyMs = busyMs
    this.idleMs = idleMs
    this.cpuCount = cpus.length
  }

  busyPct(prior: CpuTimes): Maybe<number> {
    if (
      prior.cpuCount !== this.cpuCount ||
      prior.busyMs > this.busyMs ||
      prior.idleMs > this.idleMs
    ) {
      // did the counters get reset?
      logger().warn(
        "busyPct(): Unexpected CpuTimes. If this continues, busyPct() will be incorrect." +
          InternalErrorFlag,
        {
          self: this,
          prior
        }
      )
      return
    } else {
      const busyMs = this.busyMs - prior.busyMs
      const idleMs = this.idleMs - prior.idleMs
      const totalMs = busyMs + idleMs
      return Math.round(100.0 * (busyMs / totalMs))
    }
  }
}

function busyPctFromLoad() {
  return isWin
    ? undefined
    : Math.ceil(100 * (os.loadavg()[0] / os.cpus().length)) // this may be > 100%!
}
